.navigation-bar {
}

.chat-button {
    position: relative;
    float: right;
    z-index: 1200;
    cursor: pointer;
}

.chat-icon {
    padding-top: 11px;
}

#scroll-to-top {
    position: fixed;
    z-index: 2000;
    right: 30px;
    top: 1.25em;
}

@-webkit-keyframes shake {
    from { -webkit-transform: translateY(-4px); transform: translateY(-4px); }
    to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@keyframes shake {
    from { -webkit-transform: translateY(-4px); transform: translateY(-4px); }
    to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

.chat-mention {
    -webkit-animation-name: shake;
            animation-name: shake;
    -webkit-animation-duration: .3s;
            animation-duration: .3s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(.52,.4,.75,-0.01);
            animation-timing-function: cubic-bezier(.52,.4,.75,-0.01);
}

a {
    text-decoration: none !important;
}



